body {
  background: #f5f5f5;
  justify-content: center;
  margin: 0;
  padding: 40px 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.gallery {
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  width: 100%;
  max-width: 1200px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery-item {
  overflow: hidden;
}

.gallery-link {
  width: 100%;
  height: 100%;
  display: block;
}

.gallery-image {
  object-fit: cover;
  cursor: pointer;
  border-radius: 0;
  width: 100%;
  height: 100%;
  transition: transform .3s;
  display: block;
}

.gallery-image:hover {
  transform: scale(1.05);
}
